"use client";

import React from "react";

export default function FeatureCard({ icon, title, children }) {
  return (
    <div className="card p-6 flex gap-4 items-start">
      <div className="flex-none w-12 h-12 rounded-full bg-slate-50 flex items-center justify-center">
        {icon || <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="#2563EB" strokeWidth="1.5"><circle cx="12" cy="12" r="9"/></svg>}
      </div>
      <div>
        <h4 className="text-lg font-medium">{title}</h4>
        <p className="text-sm meta mt-1">{children}</p>
      </div>
    </div>
  );
}
